"use client";

import { useEffect, useRef, useState } from "react";

interface TrackMap2DProps {
  lapData: any;
  corners: any[];
  hoveredDistance: number | null;
  currentIndex: number;
  onHoverDistance: (distance: number | null) => void;
}

export function TrackMap2D({
  lapData,
  corners,
  hoveredDistance,
  currentIndex,
  onHoverDistance,
}: TrackMap2DProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [tooltip, setTooltip] = useState<{ x: number; y: number; text: string } | null>(null);

  useEffect(() => {
    if (!canvasRef.current || !lapData?.telemetry) {
      console.log("Track map: waiting for data...", { hasCanvas: !!canvasRef.current, hasLapData: !!lapData, hasTelemetry: !!lapData?.telemetry });
      return;
    }

    const telemetry = lapData.telemetry;

    if (telemetry.length === 0) {
      console.log("Track map: no telemetry points");
      return;
    }

    console.log("Track map: rendering", telemetry.length, "points");
    console.log("First point:", telemetry[0]);

    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    const dpr = window.devicePixelRatio || 1;
    const rect = canvas.getBoundingClientRect();
    canvas.width = rect.width * dpr;
    canvas.height = rect.height * dpr;
    ctx.scale(dpr, dpr);

    const width = rect.width;
    const height = rect.height;
    const padding = 40;

    const allX = telemetry.map((p: any) => p.x);
    const allY = telemetry.map((p: any) => p.y);

    const minX = Math.min(...allX);
    const maxX = Math.max(...allX);
    const minY = Math.min(...allY);
    const maxY = Math.max(...allY);

    const rangeX = maxX - minX;
    const rangeY = maxY - minY;
    const scale = Math.min((width - padding * 2) / rangeX, (height - padding * 2) / rangeY);

    const toScreenX = (x: number) => padding + (x - minX) * scale;
    const toScreenY = (y: number) => height - (padding + (y - minY) * scale);

    ctx.fillStyle = "#0a0a0a";
    ctx.fillRect(0, 0, width, height);

    corners.forEach((corner: any) => {
      if (corner.brakingZone) {
        const bz = corner.brakingZone;
        const startIdx = telemetry.findIndex((p: any) => p.distance >= bz.entryDistance);
        const endIdx = telemetry.findIndex((p: any) => p.distance >= bz.exitDistance);

        if (startIdx >= 0 && endIdx >= 0) {
          ctx.strokeStyle = "rgba(255, 60, 60, 0.7)";
          ctx.lineWidth = 14;
          ctx.lineCap = "round";
          ctx.lineJoin = "round";

          ctx.beginPath();
          for (let i = startIdx; i <= endIdx; i++) {
            const p = telemetry[i];
            const x = toScreenX(p.x);
            const y = toScreenY(p.y);
            if (i === startIdx) {
              ctx.moveTo(x, y);
            } else {
              ctx.lineTo(x, y);
            }
          }
          ctx.stroke();
        }
      }
    });

    const maxSpeed = Math.max(...telemetry.map((p: any) => p.speed));

    ctx.lineWidth = 10;
    ctx.lineCap = "round";
    ctx.lineJoin = "round";

    for (let i = 0; i < telemetry.length - 1; i++) {
      const p1 = telemetry[i];
      const p2 = telemetry[i + 1];

      const speedRatio = p1.speed / maxSpeed;
      const hue = speedRatio * 120;
      ctx.strokeStyle = `hsla(${hue}, 100%, 60%, 1)`;

      ctx.beginPath();
      ctx.moveTo(toScreenX(p1.x), toScreenY(p1.y));
      ctx.lineTo(toScreenX(p2.x), toScreenY(p2.y));
      ctx.stroke();
    }

    corners.forEach((corner: any, idx: number) => {
      const apexPoint = telemetry.find(
        (p: any) => Math.abs(p.distance - corner.apexDistance) < 5
      );

      if (apexPoint) {
        const x = toScreenX(apexPoint.x);
        const y = toScreenY(apexPoint.y);

        ctx.fillStyle = "#000";
        ctx.beginPath();
        ctx.arc(x, y, 16, 0, Math.PI * 2);
        ctx.fill();

        ctx.fillStyle = "#fff";
        ctx.beginPath();
        ctx.arc(x, y, 14, 0, Math.PI * 2);
        ctx.fill();

        ctx.fillStyle = "#000";
        ctx.font = "bold 11px sans-serif";
        ctx.textAlign = "center";
        ctx.textBaseline = "middle";
        ctx.fillText(`${idx + 1}`, x, y);
      }
    });

    const startPoint = telemetry[0];
    const sx = toScreenX(startPoint.x);
    const sy = toScreenY(startPoint.y);

    ctx.fillStyle = "#00ff88";
    ctx.beginPath();
    ctx.arc(sx, sy, 10, 0, Math.PI * 2);
    ctx.fill();

    ctx.strokeStyle = "#000";
    ctx.lineWidth = 2;
    ctx.stroke();

    const currentPoint = telemetry[Math.floor(currentIndex)];
    if (currentPoint) {
      const cx = toScreenX(currentPoint.x);
      const cy = toScreenY(currentPoint.y);

      ctx.shadowColor = "rgba(0, 255, 255, 0.5)";
      ctx.shadowBlur = 15;

      ctx.fillStyle = "#00ffff";
      ctx.beginPath();
      ctx.arc(cx, cy, 12, 0, Math.PI * 2);
      ctx.fill();

      ctx.shadowBlur = 0;

      ctx.strokeStyle = "#000";
      ctx.lineWidth = 2;
      ctx.beginPath();
      ctx.arc(cx, cy, 12, 0, Math.PI * 2);
      ctx.stroke();

      ctx.fillStyle = "#000";
      ctx.beginPath();
      ctx.arc(cx, cy, 4, 0, Math.PI * 2);
      ctx.fill();
    }

  }, [lapData, corners, hoveredDistance, currentIndex]);

  const handleMouseMove = (e: React.MouseEvent<HTMLCanvasElement>) => {
    if (!canvasRef.current || !lapData?.telemetry) return;

    const canvas = canvasRef.current;
    const rect = canvas.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;

    const width = rect.width;
    const height = rect.height;
    const padding = 40;

    const telemetry = lapData.telemetry;
    const minX = Math.min(...telemetry.map((p: any) => p.x));
    const maxX = Math.max(...telemetry.map((p: any) => p.x));
    const minY = Math.min(...telemetry.map((p: any) => p.y));
    const maxY = Math.max(...telemetry.map((p: any) => p.y));
    const rangeX = maxX - minX;
    const rangeY = maxY - minY;
    const scale = Math.min((width - padding * 2) / rangeX, (height - padding * 2) / rangeY);

    let closestPoint: any = null;
    let closestDist = Infinity;

    telemetry.forEach((p: any) => {
      const sx = padding + (p.x - minX) * scale;
      const sy = height - (padding + (p.y - minY) * scale);
      const dist = Math.sqrt((x - sx) ** 2 + (y - sy) ** 2);

      if (dist < closestDist && dist < 20) {
        closestDist = dist;
        closestPoint = p;
      }
    });

    if (closestPoint) {
      onHoverDistance(closestPoint.distance);
      setTooltip({
        x: e.clientX,
        y: e.clientY,
        text: `${Math.round(closestPoint.distance)}m | ${Math.round(closestPoint.speed)} km/h`,
      });
    } else {
      onHoverDistance(null);
      setTooltip(null);
    }
  };

  const handleMouseLeave = () => {
    onHoverDistance(null);
    setTooltip(null);
  };

  return (
    <div className="relative rounded-xl border border-zinc-800 bg-zinc-900/50 p-6">
      <div className="mb-4 flex items-center justify-between">
        <h2 className="text-xl font-semibold text-white">Track Map</h2>
        <div className="flex items-center gap-4 text-sm">
          <div className="flex items-center gap-2">
            <div className="h-2 w-8 rounded bg-linear-to-r from-red-500 via-yellow-500 to-green-500" />
            <span className="text-zinc-400">Racing Line (speed)</span>
          </div>
          <div className="flex items-center gap-2">
            <div className="h-2 w-8 rounded bg-red-500/70" />
            <span className="text-zinc-400">Brake Zones</span>
          </div>
        </div>
      </div>

      <canvas
        ref={canvasRef}
        className="w-full rounded-lg cursor-crosshair"
        style={{ height: "700px" }}
        onMouseMove={handleMouseMove}
        onMouseLeave={handleMouseLeave}
      />

      {tooltip && (
        <div
          className="pointer-events-none fixed z-50 rounded bg-black px-3 py-2 text-sm text-white shadow-lg border border-zinc-700"
          style={{
            left: tooltip.x + 10,
            top: tooltip.y + 10,
          }}
        >
          {tooltip.text}
        </div>
      )}
    </div>
  );
}
